<template>
  <div style="width:800px;height: auto">
    <criteria-paged v-ref:page4 :model="model4" :pager='false'>
      <criteria v-ref:kaitongka partial='criteria' @condition-changed='search'>
        <div novalidate class="form-inline auto" partial>
          <div class="form-group">
            <!--<span class="btn btn-default" @click="search()"></span>-->
            <!--<report-print id='shexianss' top='1cm' left='1cm' width='100%' height='100%'></report-print>-->
            <div class="col-sm-12">
              <div class="col-sm-10"></div>
              <div class="col-sm-2">
                <report-print @click="$parent.$parent.$parent.updateNumber()" id='shexianss4' top='1cm' left='1cm' width='100%' height='100%'></report-print>
              </div>
            </div>
          </div>
        </div>
      </criteria>
      <div partial='list' id='shexianss4'>
        <table class='tablesty' style="margin: 0px auto;" border="1">
          <tr>
            <th  colspan='4' style="font-weight: normal;">
              {{{ model.data.substring(26,model.data.length-8) }}}
              <!--{{model.data}}-->
            </th>
          </tr>
        </table>

      </div>

    </criteria-paged>
  </div>
</template>

<script>
  import {DataModel} from 'vue-client'
  export default {
    title: "开通卡",
    props:['billselect'],
    data(){
      return{
        model4: new DataModel('rs/report/kaitongka', {data:{f_process_id:this.billselect.f_process_id}})
      }
    },
    ready(){
      this.$refs.page4.$refs.kaitongka.search()
    },
    watch: {
      'billselect'(){
        this.$refs.page4.$refs.kaitongka.search()
      }
    }
  }
</script>

<style scoped>

</style>



